/* Style for the Logo Container */
.logo {
  display: flex;
  align-items: center;      /* Aligns the logo image and text vertically */
  text-decoration: none;    /* Removes underline from the link */
  color: white;             /* Text color for the "Welcome" text */
  font-family: 'Poppins', sans-serif; /* Use Poppins or any desired font */
}

/* Logo Image Styling */
.logo-img {
  width: 50px;              /* Adjust the logo size */
  height: auto;             /* Maintain aspect ratio */
  margin-right: 10px;       /* Space between logo and text */
  transition: transform 0.3s ease; /* Add transition for hover effects */
}

/* Hover Effect for the Logo Image */
.logo-img:hover {
  transform: scale(1.1);    /* Slightly scale up the logo when hovered */
}

/* Styling for the Text next to the Logo */
.logo h1 {
  font-size: 2rem;          /* Adjust font size for the "Welcome" text */
  font-weight: 600;         /* Make the text bold */
  letter-spacing: 1px;      /* Add spacing between letters */
  margin: 0;                /* Remove default margin */
  color: white;             /* Make sure the text is white */
  text-transform: uppercase; /* Optional: Make the text uppercase */
}

/* Responsive Styling: Adjust Logo and Text for Small Screens */
@media screen and (max-width: 768px) {
  .logo {
    font-size: 1.5rem;       /* Reduce font size on smaller screens */
  }

  .logo-img {
    width: 40px;             /* Reduce logo size for smaller screens */
  }
}
.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 100px;
    z-index: 10;
}

.header-bg{
  background-color: rgba(0, 0, 0, 0.85);
  transition: 0.5s;
}

.nav-menu{
    display: flex;
}

.nav-menu li{
    padding: 0 2rem;
}


.nav-menu li {
  position: relative;
  overflow: hidden;
}

.nav-menu li::after {
  content: '';
  position: absolute;
  bottom: 10px;
  left: 30px;
  right: 100px;
  width: 50%;
  height: 2px;
  background-color: rgb(21, 255, 0);
  transform: scaleX(0);
  transform-origin: 0 0;
  transition: background-color 0.3s ease-in-out, transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out;
}

.nav-menu li:hover::after {
  transform: scaleX(1);
}



.nav-menu li a{
    font-size: 1.2rem;
    font-weight: 500;
}

.hamburger{
    display: none;
    transition: cubic-bezier(0.47, 0, 0.745, 0.715);
}

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Outfit", sans-serif;
}

body{
  background: #000;
}

h1, h4, p, a{
  color: #f3f3f3;
  text-decoration: none;
}

ul{
  list-style-type: none;
}

.btn{
  padding: 12px 20px;
  font-size: 1rem;
  text-transform: uppercase;
  background: white;
  color: black;
  border: 2px solid white;
  font-weight: 600;
  cursor: pointer;
}

.btn-light{
  background: transparent;
  color: #fff;
}

.btn:hover{
  background: rgba(243, 241, 246, 0.2);
  color: black;
  transition: 0.3s;
}

@media screen and (max-width: 1040px){
  .nav-menu{
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.9);
    position: absolute;
    top: 0;
    bottom: 0;
    left: -100%;
    z-index: -3;
    transition: 0.8s;
  }

  .nav-menu.active{
    left: 0;
  }

  .nav-menu li{
    padding: 1rem 0;
  }

  .nav-menu li a{
    font-size: 2rem;
  }

  .hamburger{
    display: initial;
  }
  
}
.mask{
    width: 100%;
    height: 100vh;
    position: relative;
}

.into-img{
    width: 100%;
    height: 100vh;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}

.mask::after{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #000;
    opacity: 0.8;
}

.hero{
    height: 100%;
    width: 100%;
}

.hero .content{
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: left;
}

.content h3 {
    font-size: 2.5rem;
    color: green;
    border-right: 2px solid green;
    white-space: nowrap;
    overflow: hidden;
    width: 0;
    display: inline-block;
    box-sizing: border-box;
}

.type1 {
    animation: typing 3.5s steps(30, end) 0.5s forwards, blink-caret 0.75s step-end infinite;
}

.type2 {
    animation: typing 3.5s steps(30, end) 4.5s forwards, blink-caret 0.75s step-end infinite;
}

@keyframes typing {
    from { width: 0; }
    to { width: 100%; }
}

@keyframes blink-caret {
    50% {
        border-color: transparent;
    }
}

@media (max-width: 768px) {
    .content h3 {
        font-size: 1.8rem;
    }
}

@media (max-width: 480px) {
    .content h3 {
        font-size: 1.5rem;
    }
}

.herobtn {
    margin-top: 20px;
}

.hero .content p{
    font-size: 1.5rem;
    font-weight: 200;
    text-transform: uppercase;

}

.content .btn{
    margin: 1.5rem 0.2rem;
}

@media screen and (max-width:640px){
    .hero .content h4{
        font-size: 3rem;
    }

    .hero .content p{
        font-size: 1.4rem;
    }
    
}

