/* Styles for larger screens (desktop/tablet) */
.about {
    width: 100%;
    margin: 3rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    height: 100%;
}

.about .left {
    text-align: center;
    margin: auto;
    padding: 1rem;
    max-width: 350px;
}

.about .left p {
    margin: 1.2rem 0;
}

.about .right {
    max-width: 700px;
}

.right .img-container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    position: relative;
    align-items: center;
    text-align: center;
}

.right .img {
    max-width: 90%;
    border: 1px solid #333;
}

.right .top {
    grid-column: 1 / span 8;
    grid-row: 1;
    padding-top: 20%;
    z-index: 6;
}

.right .bottom {
    grid-column: 4 / -1;
    grid-row: 1;
    z-index: 5;
}

.img-container {
    display: flex;
    flex-direction: column;
    height: 100%;
  }
  
  .img-stack {
    flex: 1 1;
    max-width: 100%;
    height: auto;
  }
  
  @media (max-width: 768px) {
    .img-container {
      flex-direction: row;
    }
  
    .img-stack {
      flex: 1 1;
      max-width: 50%;
    }
  }
  

@media screen and (max-width: 768px) {
    .about {
        flex-direction: column; 
    }
    
    .left {
        flex: none; 
        margin-right: 0;
    }
    
    .right {
        flex: none;
    }
  }
/* Pricing Cards Styles */
.pricing {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 50px 0;
    padding: 0 20px;
  }
  
  .pricing-title {
    font-size: 2.5rem;
    font-weight: bold;
    text-align: center;
    margin-bottom: 40px;
  }
  
  .card-container {
    display: flex;
    justify-content: center;
    grid-gap: 20px;
    gap: 20px;
    flex-wrap: wrap;
  }
  
  .card {
    background-color: #fff;
    border: 1px solid #ddd;
    padding: 30px;
    border-radius: 10px;
    width: 280px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
  }
  
  .card:hover {
    transform: translateY(-10px);
  }
  
  .card h3 {
    font-size: 1.8rem;
    font-weight: 600;
    color: #333;
    margin-bottom: 20px;
  }
  
  .bar {
    display: block;
    width: 50px;
    height: 3px;
    background-color: #f57c00;
    margin: 10px auto;
  }
  
  .btc {
    font-size: 2rem;
    font-weight: bold;
    color: #f57c00;
    margin-bottom: 15px;
  }
  
  .card p {
    font-size: 1rem;
    margin: 10px 0;
    color: #555;
  }
  
  .btn {
    display: inline-block;
    padding: 10px 20px;
    background-color: #eceee7;
    color: #000000;
    text-align: center;
    text-decoration: none;
    border-radius: 5px;
    margin-top: 20px;
    transition: background-color 0.3s ease;
  }
  
  .btn:hover {
    background-color: #4c6292;
  }
  
  @media (max-width: 768px) {
    .card-container {
      flex-direction: column;
      align-items: center;
    }
  }
  
